<template>
    <div class="table-class-style">
        <demo-row title="rowClassFunc">
            <demo-line title="评分大于5的显示背景蓝色"/>
            <pl-table :data="data" :rowClassFunc="rowClassFunc">
                <plc field="id" title="编号"/>
                <plc field="size" title="大小"/>
                <plc field="date" title="日期"/>
                <plc field="color" title="颜色"/>
                <plc field="name" title="名称"/>
                <plc field="star" title="评分"/>
            </pl-table>
        </demo-row>

        <demo-row title="cellClassFunc">
            <demo-line title="评分大于5的显示背景蓝色"/>
            <pl-table :data="data" :cellClassFunc="cellClassFunc">
                <plc field="id" title="编号"/>
                <plc field="size" title="大小"/>
                <plc field="date" title="日期"/>
                <plc field="color" title="颜色"/>
                <plc field="name" title="名称"/>
                <plc field="star" title="评分"/>
            </pl-table>
        </demo-row>
        <demo-row title="cellStyleFunc">
            <demo-line title="评分大于5的蓝色加粗字体"/>
            <pl-table :data="data" :cellStyleFunc="cellStyleFunc">
                <plc field="id" title="编号"/>
                <plc field="size" title="大小"/>
                <plc field="date" title="日期"/>
                <plc field="color" title="颜色"/>
                <plc field="name" title="名称"/>
                <plc field="star" title="评分"/>
            </pl-table>
        </demo-row>
        <demo-row title="headCellClassFunc">

            <pl-table :data="data" :headCellClassFunc="headCellClassFunc">
                <plc field="id" title="编号"/>
                <plc field="size" title="大小"/>
                <plc field="date" title="日期"/>
                <plc field="color" title="颜色"/>
                <plc field="name" title="名称"/>
                <plc field="star" title="评分"/>
            </pl-table>
        </demo-row>
        <demo-row title="headCellStyleFunc">

            <pl-table :data="data" :headCellStyleFunc="headCellStyleFunc">
                <plc field="id" title="编号"/>
                <plc field="size" title="大小"/>
                <plc field="date" title="日期"/>
                <plc field="color" title="颜色"/>
                <plc field="name" title="名称"/>
                <plc field="star" title="评分"/>
            </pl-table>
        </demo-row>
    </div>
</template>

<script>

    import data from "../data/data-1.json";

    export default {
        name: "table-class-style",
        data() {
            return {
                data,
            }
        },
        methods: {
            rowClassFunc({data}) {
                const {star} = data
                return [
                    'custom-row',
                    `custom-row-status-${star > 5 ? 'active' : 'inactive'}`
                ]
            },
            cellClassFunc({data}, plc) {
                const {star} = data

                if (plc.props.field === 'star') {
                    return [
                        'custom-cell',
                        `custom-cell-status-${star > 5 ? 'active' : 'inactive'}`
                    ]
                }
            },
            cellStyleFunc({data: {star}}, {props: {field}}) {
                if (field === 'star') {
                    return {
                        color: star > 5 ? '#12b4a5' : '',
                        fontWeight: star > 5 ? 'bold' : 'normal'
                    }
                }
            },
            headCellClassFunc({props: {field}}) {
                if (field === 'star') {
                    return 'custom-head-cell-clz'
                }
            },
            headCellStyleFunc({props: {field}}) {
                if (field === 'star') {
                    return {
                        backgroundColor: '#3E97EC',
                        color: 'white',
                    }
                }
            },
        }
    }
</script>

<style lang="scss">
    .custom-row {
        &.custom-row-status-active {
            td {
                background-color: rgba(#3E97EC, 0.1) !important;
            }
        }
    }

    .custom-cell {
        &.custom-cell-status-active {
            background-color: rgba(#3E97EC, 0.1) !important;
        }
    }

    .custom-head-cell-clz {
        background-color: #3E97EC !important;
        color: white !important;
    }
</style>